import React from 'react';
import {
    BordererPresets,
    Flexer,
    FlexerPresets,
    FlexWrap,
    Fonter,
    MarginerPresets, Shadower, ShadowerPresets,
    shiftBreakpoint,
    Surface,
    TextAlign, useBreakpoint
} from '@spinglobal/react-foundations';
import {HeaderPrimary, TextPrimary} from '../../Components';
import PartnersList from './partnersList';

const paragraph1 = 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor in';

export function Partners() {
    const bp = useBreakpoint();
    return <Surface style={MarginerPresets.auto_4}>
        <Surface style={FlexerPresets.ColumnCenter}>
            <HeaderPrimary text='Our Partners' style={Fonter({textAlign: TextAlign.Center})}/>
            <TextPrimary
                text={paragraph1}
                style={{
                    ...MarginerPresets._4,
                    maxWidth: shiftBreakpoint(bp, -2),
                    ...Fonter({textAlign: TextAlign.Center})
                }}/>
        </Surface>
        <Surface style={{
            ...FlexerPresets.MainCenter, ...Flexer({wrap: FlexWrap.Wrap}),
            maxWidth: shiftBreakpoint(bp, -1), ...MarginerPresets.auto
        }}>
            {PartnersList.map(e => {
                return <Surface style={{
                    backgroundImage: `url(${e.src})`,
                    backgroundSize: 'cover',
                    width: '10vw',
                    height: '10vw',
                    minWidth: 96,
                    minHeight: 96,
                    ...MarginerPresets._2,
                    ...BordererPresets.P50,
                    ...Shadower({voffset: 2, hoffset: 2, blur: 4})
                }}/>
            })}
        </Surface>
    </Surface>;
}
